	module.exports = {"data":{"markdownRemark":{"html":"<p>中央政府と地方自治体の関係の写本についてのもう一つのコメントがなされた。中央権力の性質は、</p>\n<p>中央政府の政治的管轄は国家主権の徴候であり、すべての地方や部門は徹底的に遵守し、違反に違反することはできません。当然、違反はない。</p>\n<p>自分の管轄権を行使する際には、すべての地方自治体および部局が、自らの権力行為の影響および影響を慎重に分析し判断し、意識して中央当局を弱め、損なう行為を防止しなければならない。</p>\n<p>基本的原則の違反を真剣に制限し処罰しながら、明確なシステムを確立し、すべてのレベルの管轄が定義されたシステムで使用されるようにするための完全な法的基準を策定する。</p>\n<p>要員の組織化においては、中央政府の権限を維持するための保証が必要であり、中央当局の違反を是正しない者および機関は、法律および規律に従って処理されなければならない。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0124.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0124.md","frontmatter":{"title":"7月22日 金曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0124.md absPath of file >>> MarkdownRemark"}}

